@import url(https://fonts.googleapis.com/css2?family=Nunito&display=swap);
/* global css variables */
:root {
  --primary-color: #ea7773;
  --secondary-color: #f86863;
  --dark-color: #0f1418;
  --light-color: #f4f4f4;
}
/* css resets */

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: "Nunito", sans-serif;
  color: #333;
  line-height: 1.6;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

ul {
  list-style-type: none;
}

a {
  text-decoration: none;
  color: #333;
}

h1,
h2 {
  font-weight: 500;
  line-height: 1.2;
  margin: 10px 0;
}
p {
  margin: 10px 0;
}
img {
  width: 100%;
}



/* common utility classes */

.container {
  max-width: 1100px;
  margin: 0 auto;
  overflow: auto;
  padding: 0 40px;
}

.flex {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
}

.grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 20px;
  gap: 20px;
  justify-content: center;
  align-items: center;
  height: 100%;
}

.grid-3 {
  grid-template-columns: repeat(3, 1fr);
}

.card {
  background-color: #fff;
  border-radius: 10px;
  box-shadow: 0 3px 10px rgba(0, 0, 0, 0.2);
  padding: 20px;
  margin: 10px;
  color: #333;
}

.btn {
  display: inline-block;
  padding: 10px 30px;
  cursor: pointer;
  background-color: var(--primary-color);
  color: #fff;
  border: none;
  border-radius: 5px;
  outline: none;
}

.btn-outline {
  background-color: transparent;
  border: 1px white solid;
}

.btn:hover {
  transform: scale(0.98);
}

.text-center {
  text-align: center;
}

/* margins */

.my-1 {
  margin: 1rem 0;
}
.my-2 {
  margin: 1.5rem 0;
}
.my-3 {
  margin: 2rem 0;
}
.my-4 {
  margin: 3rem 0;
}
.my-5 {
  margin: 4rem 0;
}

.m-1 {
  margin: 1rem;
}
.m-2 {
  margin: 1.5rem;
}
.m-3 {
  margin: 2rem;
}
.m-4 {
  margin: 3rem;
}
.m-5 {
  margin: 4rem;
}

/* padding */
.py-1 {
  margin: 1rem 0;
}
.py-2 {
  margin: 1.5rem 0;
}
.py-3 {
  margin: 2rem 0;
}
.py-4 {
  margin: 3rem 0;
}
.py-5 {
  margin: 4rem 0;
}

.p-1 {
  padding: 1rem;
}
.p-2 {
  padding: 1.5rem;
}
.p-3 {
  padding: 2rem;
}
.p-4 {
  padding: 3rem;
}
.p-5 {
  padding: 4rem;
}

/* backgrouds */
.bg-primary,
.btn-primary {
  background-color: var(--primary-color);
  color: #fff;
}

.bg-secondary,
.btn-secondary {
  background-color: var(--secondary-color);
  color: #fff;
}

.bg-dark,
.btn-dark {
  background-color: var(--dark-color);
  color: #fff;
}

.bg-light,
.btn-light {
  background-color: var(--light-color);
  color: #fff;
}

.bg-primary a,
.btn-primary a,
.bg-secondary a,
.btn-secondary a,
.bg-dark a,
.btn-dark a {
  color: #fff;
}
/* text classes */

.lead {
  font-size: 20px;
}

.sm {
  font-size: 1rem;
}

.md {
  font-size: 2rem;
}
.lg {
  font-size: 3rem;
}
.xl {
  font-size: 4rem;
}

.header {
  height: 500px;
  background-color: var(--primary-color);
  color: #fff;
}

.header img {
  height: 100%;
}

.header .grid {
  grid-template-rows: repeat(3, 1fr);
}

.header .grid > *:nth-child(2) {
  grid-column: 2;
  grid-row: 1 / span 3;
}

.navbar {
  background-color: var(--primary-color);
  color: #fff;
  height: 70px;
}

.navbar .flex {
  justify-content: space-between;
}

.navbar ul {
  display: flex;
}

.navbar ul li button {
  color: #fff;
  width: 150px;
  padding: 10px;
  margin: 0 5px;
  font-size: large;
}

.navbar .logo {
  display: flex;
  justify-content: center;
  align-items: center;
}

.navbar .logo i {
  margin-right: 10px;
}

.features {
  height: 350px;
  background-color: var(--light-color);
}

.features .flex {
  flex-wrap: wrap;
}
.features .card {
  text-align: center;
  height: 270px;
  width: 250px;
  margin: 0 40px;
  transition: transform 0.2s ease-in;
}
.features .card h3 {
  margin-top: 20px;
}
.features .card p {
  padding: 15px;
}

.features .card:hover {
  transform: translateY(-18px);
}

.features .card i {
  color: var(--secondary-color);
}

.showcase {
  height: 690px;
}

.showcase img {
  height: 100%;
}

.showcase .grid {
  grid-template-columns: 1fr 4fr;
  grid-gap: 0;
  gap: 0;
}

.showcase .grid .grid {
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, 1fr);
}

.showcase .grid .grid > *:first-child {
  grid-row: 1 / span 2;
}

.footer {
  background-color: var(--dark-color);
  height: 250px;
  color: #fff;
}

.footer .grid {
  grid-template-columns: repeat(3, 1fr);
}

.footer h4 {
  margin-bottom: 15px;
}

.modal {
  position: fixed;
  z-index: 500;
  background-color: #f4f4f4;
  width: 70%;
  border: 1px solid #ccc;
  box-shadow: 1px 1px 1px #333;
  padding: 16px;
  left: 15%;
  top: 25%;
  border-radius: 5px;
  transition: all 0.3s ease-out;
}

@media (min-width: 600px) {
  .modal {
    width: 500px;
    left: calc(50% - 250px);
  }
}

.backdrop {
  width: 100%;
  height: 100%;
  position: fixed;
  z-index: 100;
  left: 0;
  top: 0;
  background-color: rgba(0, 0, 0, 0.6);
}

.login {
  text-align: center;
}
.login .form-control {
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin: 30px 0;
}

.login .form-control input {
  border: 0;
  border-bottom: 1px solid #b4becb;
  width: 80%;
  padding: 15px;
  font-size: 16px;
  border-radius: 5px;
}

.login .form-control input:focus {
  outline: none;
}

.signup {
  text-align: center;
}
.signup .form-control {
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin: 30px 0;
}

.signup .form-control input {
  border: 0;
  border-bottom: 1px solid #b4becb;
  width: 80%;
  padding: 15px;
  font-size: 16px;
  border-radius: 5px;
}

.signup .form-control input:focus {
  outline: none;
}


.appbar {
  background-color: var(--primary-color);
  color: #fff;
  height: 70px;
}

.appbar .container {
  max-width: 1300px;
}

.appbar .flex {
  justify-content: space-between;
}

.appbar nav ul {
  display: flex;
  align-items: center;
}

.appbar nav ul li i {
  padding: 10px;
}
.appbar nav ul li {
  display: flex;
  padding: 10px;
  font-size: 1rem;
  justify-content: space-evenly;
  align-items: center;
}
.appbar ul li button {
  color: #fff;
  width: 150px;
  padding: 10px;
  margin: 0 5px;
  font-size: large;
}

.appbar .logo {
  display: flex;
  justify-content: center;
  align-items: center;
}

.appbar .logo i {
  margin-right: 10px;
}

.restaurantItems .grid {
  grid-template-columns: repeat(3, 1fr);
}

.restaurantItems .container {
  max-width: 1200px;
}

.restaurantItem .card .grid {
  grid-template-columns: repeat(3, 1fr);
}

.restaurantItem .card .grid > *:first-child {
  grid-column: 1 / span 3;
}

.restaurantItem .card .grid > *:nth-child(2) {
  grid-column: 1 / span 2;
}

.restaurantItem .card .grid > *:nth-child(4) {
  grid-column: 1 / span 3;
}

.restaurantItem .card .grid > *:nth-child(5) {
  grid-column: 1 / span 2;
}

.restaurantItem .card .grid img {
  width: 100%;
  height: 200px;
}

.restaurantItem .card .btn {
  width: 130px;
}

